.services {
    padding: 100px 0 70px;
    clear: both;

    @media screen and (max-width: 992px) {
        padding: 40px 0 70px;
    }

    @media screen and (max-width: 768px) {
        padding: 40px 0 60px;
    }

    @media screen and (max-width: 560px) {
        padding-bottom: 40px;
    }

    &__custom-container {
        //max-width: 1120px;
        padding: 0 20px;
        @media screen and (max-width: 1200px) {
            padding: 0;
        }
    }

    &__title,
    &__subtitle,
    &__text {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin-bottom: 0;
        }
    }

    &__title {
        color: #17181a;
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Turbine, sans-serif;
        font-size: 90px;
        font-style: normal;
        font-weight: 700;
        line-height: 1.11;
        margin-bottom: 8px;
        text-align: center;

        @media screen and (max-width: 1530px) {
            font-size: 66px;
        }
        @media screen and (max-width: 992px) {
            font-size: 60px;
        }

        @media screen and (max-width: 768px) {
            font-size: 50px;
        }

        @media screen and (max-width: 560px) {
            font-size: 40px;
            margin-bottom: 16px;
        }
    }

    &__subtitle {
        color: #2f3034;
        font-family: Turbine, sans-serif;
        font-size: 76px;
        font-style: normal;
        font-weight: 300;
        line-height: 1.16;
        margin-bottom: 8px;
        text-align: center;

        @media screen and (max-width: 1530px) {
            font-size: 52px;
        }

        @media screen and (max-width: 1200px) {
            font-size: 45px;
        }

        @media screen and (max-width: 992px) {
            font-size: 35px;
        }

        @media screen and (max-width: 768px) {
            font-size: 25px;
        }

        @media screen and (max-width: 560px) {
            font-size: 18px;
        }
    }

    &__text {
        color: #46494d;
        text-align: center;
        font-family: Turbine, sans-serif;
        font-size: 46px;
        font-style: normal;
        font-weight: 300;
        line-height: 1.26;
        margin-bottom: 57px;

        @media screen and (max-width: 1530px) {
            font-size: 30px;
        }

        @media screen and (max-width: 1200px) {
            font-size: 28px;
        }

        @media screen and (max-width: 992px) {
            font-size: 24px;
            margin-bottom: 45px;
        }

        @media screen and (max-width: 768px) {
            font-size: 18px;
            margin-bottom: 40px;

            br {
                display: none;
            }
        }

        @media screen and (max-width: 560px) {
            font-size: 14px;
            margin-bottom: 32px;
        }
    }


    &__wrap {
        padding: 32px 0 0;
        display: flex;
        justify-content: center;
        gap:32px;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: 30px;

        @media screen and (max-width: 992px) {
            gap: 16px;
            justify-content: center;
        }

        @media screen and (max-width: 380px) {
            flex-direction: column;
        }
    }

    &__item-wrapper {
        position: relative;
        height: auto;
        aspect-ratio: 1;
        width: 238px;
        max-width: 25%;
        @media screen and (max-width: 992px) {
            max-width: 50%;
        }

        @media screen and (max-width: 560px) {
            width: 155px;
        }

        @media screen and (max-width: 380px) {
            max-width: 100%;
        }

        &:before {
            content: "";
            aspect-ratio: 1;
            width: 358px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-49.4%, -50.7%);
            background-image: url("data:image/svg+xml;base64,");
            transition: opacity .3s;
            opacity: 0;
            pointer-events: none;
            @media screen and (max-width: 560px) {
                width: 230px;
            }
        }

        &:hover {
            &:before {
                opacity: 1;
            }
        }
    }

    &__item {
        position: relative;
        padding-top: 18px;
        display: flex;
        flex-direction: column;
        align-items: center;
        clip-path: url(#svg-clip-238);
        text-decoration: none;
        height: 100%;
        width: 100%;
        aspect-ratio: 1;
        margin: 0 auto;

        @media screen and (max-width: 560px) {
            clip-path: url(#svg-clip-155);
            padding-top: 15px;
        }

        & * {
            pointer-events: none;
        }

        &-icon {
            height: 63px;
            width: 63px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 8px;

            img {
                max-width: 100%;
                height: auto;
            }

            @media screen and (max-width: 560px) {
                width: 40px;
                height: 40px;
                margin-bottom: 4px;
            }
        }

        &-title,
        &-desc {

            p {
                color: inherit;
                font-feature-settings: inherit;
                font-family: inherit;
                font-size: inherit;
                font-style: inherit;
                font-weight: inherit;
                line-height: inherit;
                text-align: inherit;
                margin: inherit;
            }

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                margin-bottom: 0;
            }
        }

        &-title {
            color: #f3f0ec;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 20px;
            font-style: normal;
            font-weight: 700;
            line-height: 1.6;
            text-align: center;
            margin: 0 0 5px 0;

            @media screen and (max-width: 768px) {
                font-size: 18px;
            }

            @media screen and (max-width: 560px) {
                font-size: 16px;
                line-height: 1.12;
            }

            p {
                margin-bottom: 0;
            }
        }

        &-desc {
            color: #f3f0ec;
            text-align: center;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 18px;
            font-style: normal;
            font-weight: 500;
            line-height: 1.13;
            margin-bottom: 8px;

            @media screen and (max-width: 992px) {
                font-size: 16px;
            }

            @media screen and (max-width: 768px) {
                font-size: 14px;
            }

            @media screen and (max-width: 560px) {
                font-size: 12px;
                line-height: 1.16;
            }
        }

        &-button {
            color: var(--Orange, #cc7510);
            font-family: Turbine, sans-serif;
            font-size: 12px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            text-align: center;
            width: 100%;
            height: 32px;
            border: none;
            position: absolute;
            z-index: 1;
            bottom: 0;
            margin-top: 30px;

            @media screen and (max-width: 560px) {
                font-size: 14px;
                height: 24px;
            }

            span {
                position: relative;
                z-index: 2;
            }

            &:before {
                background: url(data:image/svg+xml;base64,);

                //background: #17181a;
                //border-radius: 100% 100% 0 0;
                content: "";
                height: 42px;
                left: 50%;
                position: absolute;
                top: 0;
                transform: translateX(-50%);
                width: 170px;

                @media screen and (max-width: 560px) {
                    height: 42px;
                }
            }
        }
    }

    &__bottom {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 42px;
        align-items: center;

        @media screen and (max-width: 560px) {
            display: block;
        }

        &.single-column {
            grid-template-columns: 1fr;
        }

        &-title,
        &-content {
            p {
                color: inherit;
                font-feature-settings: inherit;
                font-family: inherit;
                font-size: inherit;
                font-style: inherit;
                font-weight: inherit;
                line-height: inherit;
                text-align: inherit;
            }

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                margin-bottom: 0;
            }
        }

        &-title {
            color: #17181a;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 86px;
            font-style: normal;
            font-weight: 500;
            line-height: 1.153;
            margin: 0;

            @media screen and (max-width: 1530px) {
                font-size: 52px;
            }
            @media screen and (max-width: 1200px) {
                font-size: 45px;
            }

            @media screen and (max-width: 992px) {
                font-size: 40px;
            }

            @media screen and (max-width: 768px) {
                font-size: 35px;
            }

            @media screen and (max-width: 560px) {
                font-size: 30px;
                line-height: 1.2;
                margin-bottom: 16px;
            }
        }

        &-content {
            color: #2f3034;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            margin: 0;
            line-height: 1.3;

            @media screen and (max-width: 992px) {
                font-size: 18px;
            }

            @media screen and (max-width: 768px) {
                font-size: 16px;
            }

            @media screen and (max-width: 560px) {
                font-size: 14px;
                line-height: 1.16;
            }
        }
    }
}


.site-header__max-words {
    .services__item-button:before {
        filter: invert(1);
    }
}
